* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

@import url('https://fonts.googleapis.com/css?family=Quicksand:400,700');

html {
  font-size: 100%;
} /*16px*/

body {
  background: white;
  font-family: 'Quicksand', sans-serif;
  font-weight: 400;
  line-height: 1.75;
  color: #ef4444;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: 'Quicksand', sans-serif;
  font-weight: 700;
  line-height: 1.3;
}

h1 {
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
}

h4 {
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

small,
.text_small {
  font-size: 0.8rem;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}
